[tabindex="-1"]:focus {
	outline: none !important;
}

.u-clearfix:after {
	display: table;
	clear: both;
	content: " ";
}

.u-sr-only {
	border: 0;
	position: absolute;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
}

.u-sr-only.focusable:active,
.u-sr-only.focusable:focus {
	position: static;
	overflow: visible;
	clip: auto;
	width: auto;
	height: auto;
	margin: 0;
}

.u-hidden {
	display: none !important;
}

.u-hidden-xs-up {
	display: none !important;
}

@media (max-width: 575px) {
	.u-hidden-xs-down {
		display: none !important;
	}
}

@media (min-width: 576px) {
	.u-hidden-sm-up {
		display: none !important;
	}
}

@media (max-width: 767px) {
	.u-hidden-sm-down {
		display: none !important;
	}
}

@media (min-width: 768px) {
	.u-hidden-md-up {
		display: none !important;
	}
}

@media (max-width: 991px) {
	.u-hidden-md-down {
		display: none !important;
	}
}

@media (min-width: 992px) {
	.u-hidden-lg-up {
		display: none !important;
	}
}

@media (max-width: 1199px) {
	.u-hidden-lg-down {
		display: none !important;
	}
}

@media (min-width: 1200px) {
	.u-hidden-xl-up {
		display: none !important;
	}
}

.u-hidden-xl-down {
	display: none !important;
}

@media print {
	*,
	*:before,
	*:after,
	*:first-letter,
	*:first-line {
		background: transparent !important;
		box-shadow: none !important;
		text-shadow: none !important;
		color: #000000 !important;
	}
	a,
	a:visited {
		text-decoration: underline;
	}
	a[href]:after {
		content: " (" attr(href) ")";
	}
	abbr[title]:after {
		content: " (" attr(title) ")";
	}
	a[href^="#"]:after,
	a[href^="javascript:"]:after {
		content: "";
	}
	pre,
	blockquote {
		border: 1px solid #9e9e9e;
		page-break-inside: avoid;
	}
	thead {
		display: table-header-group;
	}
	tr,
	img {
		page-break-inside: avoid;
	}
	img {
		max-width: 100% !important;
	}
	p,
	h2,
	h3 {
		orphans: 3;
		widows: 3;
	}
	h2,
	h3 {
		page-break-after: avoid;
	}
}
